/* Dark mode styles */

/* dark-mode.css */
html.dark-mode {
  background-color: black;
  padding: 0;
  margin: 0;
}

.dark-mode {
  background-color: #111;
  color: #eee;
}

/* Dark mode toggle switch */
.dark-mode-toggle {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
}

.dark-mode-toggle label {
  display: inline-block;
  position: relative;
  margin-right: 10px;
  cursor: pointer;
}

.dark-mode-toggle input {
  display: none;
}

.dark-mode-toggle .slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 16px;
  pointer-events: none;
  transition: 0.4s;
}

.dark-mode-toggle .slider:before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 22px;
  height: 22px;
  background-color: #fff;
  border-radius: 50%;
  transition: 0.4s;
}

.dark-mode-toggle input:checked + .slider {
  background-color: #4caf50;
}

.dark-mode-toggle input:checked + .slider:before {
  transform: translateX(24px);
}
